<template>
  <div style="height:100%;">
    <!-- wrap -->
    <div id="wrap" :key="getObituaryList">
      <!-- header -->
      <header id="sub">
        <div class="header_wrap">
          <h1>부고검색</h1>
          <a @click="obituaryReg">
            <div class="reg_btn">
              <div class="reg_btn_txt">부고 등록</div>
            </div>
          </a>
        </div>
      </header>
      <!-- header -->
      <!-- search area -->
      <div id="search_area">
        <select class="selectStyle" v-model="searchType">
          <option value="name">고인명</option>
          <option value="master">상주명</option>
          <option value="phone">상주전화번호</option>
        </select>
        <div class="searchinput">
          <input
            type="text"
            placeholder="검색어를 입력해주세요"
            class="inputStyle"
            v-model="searchTxt"
            @keyup.enter="requestSearch()"
          />
          <button class="searchBtnStyle" @click="requestSearch()">
            검색버튼
          </button>
        </div>
      </div>
      <!-- //search area -->
      <div class="bugo_tab_area">
        <ul class="bugo_tab">
          <li
            @click="requestSearch('ing')"
            :class="endFlag === 'ing' ? 'active' : ''"
          >
            장례식중
          </li>
          <li
            @click="requestSearch('end')"
            :class="endFlag === 'end' ? 'active' : ''"
          >
            발인
          </li>
          <li
            @click="requestSearch('all')"
            :class="endFlag === 'all' ? 'active' : ''"
          >
            전체
          </li>
        </ul>
      </div>
      <!-- content -->
      <div :class="['bugo_tab_contents bugo-first', bugoAddCladd]">
        <!-- 전체 -->
        <div
          v-for="(obituary, index) in getObituaryList"
          :key="index"
          class="bugo_list"
        >
          <p class="style01">
            <span v-if="memberInfo.memberAttr === 'TYPE-0'">{{
              obituary.obituaryMortuary !== null
                ? obituary.obituaryMortuary.mortuaryNumber
                : "빈소없음"
            }}</span>
            <span v-if="memberInfo.memberAttr === 'TYPE-1'">{{
              obituary.obituaryAddrName +
                " " +
                (obituary.obituaryMortuaryName !== null
                  ? obituary.obituaryMortuaryName
                  : "")
            }}</span>
            <span v-if="obituary.endFlag === 'Y'">장례중</span>
            <span v-if="obituary.endFlag === 'N'" class="vacancy">발인</span>
          </p>
          <p class="style02">
            <span>고인명 : {{ obituary.obituaryName }}</span>

            <template
              v-if="
                obituary.obituaryObituaryMember === null ||
                  obituary.obituaryObituaryMember === undefined ||
                  obituary.obituaryObituaryMember.length === 0
              "
            >
              <span> : 상주없음 </span>
            </template>
            <template
              v-else
              v-for="(obituaryMember, index) in obituary.obituaryObituaryMember"
            >
              <span v-if="obituaryMember.obituaryMemberLeader" :key="index">
                : {{ obituaryMember.obituaryMemberName }} 외
                {{ obituary.obituaryObituaryMember.length }}명
              </span>
            </template>
          </p>
          <p class="style03">
            <span
              >발인일시 :
              <span class="bugo_number">{{
                YYSLASHDate(obituary.obituaryEndDate)
              }}</span></span
            >
            <span
              >등록일 :
              <span class="bugo_number">{{
                YYSLASHDate(obituary.regDate)
              }}</span></span
            >
          </p>
          <!--<p class="style04">
						<span @click="obituaryUpdate(obituary.id)">부고수정</span>
						<span  @click="obituaryView(obituary)" style="margin-left:6px">부고보기</span>
						<span v-if="obituary.obituaryObituaryMember!==null && obituary.obituaryObituaryMember!==undefined && obituary.obituaryObituaryMember.length!==0" @click="obituaryTrans(obituary.obituaryObituaryMember)" style="margin-left:6px">개별발송</span>
						<span @click="obituaryReply(obituary.id)" style="margin-left:6px">답조글</span>
						<button class="btn_style02" @click="obituaryDelete(obituary)" style="margin-left:6px">삭제</button>
						<button class="btn_style01" @click="obituaryView(obituary)">보기</button>
						<button class="btn_style02" @click="obituaryDelete(obituary)">삭제</button>
						<button v-if="obituary.obituaryObituaryMember!==null && obituary.obituaryObituaryMember!==undefined && obituary.obituaryObituaryMember.length!==0" class="btn_style03" @click="obituaryTrans(obituary.obituaryObituaryMember)">문자전송</button>
						<button class="btn_style04" @click="obituaryReply(obituary.id)">답례글</button>
						
					</p>
					-->
          <p class="style05">
            <button @click="obituaryUpdate(obituary.id)" class="btn_style05">
              부고수정
            </button>
            <button @click="obituaryView(obituary)" class="btn_style05">
              부고보기
            </button>
            <button
              v-if="
                obituary.obituaryObituaryMember !== null &&
                  obituary.obituaryObituaryMember !== undefined &&
                  obituary.obituaryObituaryMember.length !== 0
              "
              @click="
                obituaryTrans(obituary.obituaryObituaryMember, 'obituary')
              "
              class="btn_style05"
            >
              개별발송
            </button>
            <button
              v-if="
                obituary.obituaryObituaryMember !== null &&
                  obituary.obituaryObituaryMember !== undefined &&
                  obituary.obituaryObituaryMember.length !== 0
              "
              @click="
                obituaryTrans(obituary.obituaryObituaryMember, 'obituaryAll')
              "
              class="btn_style05"
            >
              단체발송
            </button>
            <button
              v-if="
                (memberInfo.id === '0230102000' || memberInfo.id === 'test2') &&
                  obituary.obituaryObituaryMember !== null &&
                  obituary.obituaryObituaryMember !== undefined &&
                  obituary.obituaryObituaryMember.length !== 0 &&
                  (obituary.obituaryCorp === null ||
                    obituary.obituaryCorp === undefined ||
                    obituary.obituaryCorp.codeId !== 'CORP-20')
              "
              @click="obituaryTrans(obituary.obituaryObituaryMember, 'movie')"
              class="btn_style05"
            >
              추모영상
            </button>
            <button
              @click="
                obituaryReply(obituary.obituaryObituaryMember, obituary.id)
              "
              class="btn_style05"
            >
              답조글
            </button>
            <button
              v-if="obituary.endFlag === 'Y' || true"
              @click="obituaryDelete(obituary)"
              class="btn_style05"
            >
              발인
            </button>
          </p>
        </div>
      </div>
      <div id="contents">
        <div class="paging">
          <button
            v-if="startPage !== 1"
            @click="onPageChange(startPage - 1)"
            class="prev"
          >
            이전
          </button>
          <a
            :class="page === getObituaryPage ? 'on' : ''"
            v-for="(page, index) in pages()"
            :key="index"
            @click="onPageChange(page)"
            >{{ page }}</a
          >
          <button
            v-if="getObituaryPageTotal > endPage"
            @click="onPageChange(endPage + 1)"
            class="next"
          >
            다음
          </button>
        </div>
      </div>
      <!-- content -->
      <Footer />
    </div>
    <MobileSmsPush
      v-if="mobileUserPopupFlag"
      :mobileSmsType="mobileSmsType"
      :pitems="obituaryUserItems"
      :transClose="transClose"
      :obituaryId="obituaryId"
    />
  </div>
</template>

<script>
import Footer from "@/components/common/Footer";
import MobileSmsPush from "@/components/obituary/MobileSmsPush";

import myMixin from "@/mixin/mixin";

import { mapGetters } from "vuex";

export default {
  name: "ObituartList",
  mixins: [myMixin],
  components: {
    Footer,
    MobileSmsPush
  },
  data() {
    return {
      mobileSmsType: "",
      mobileUserPopupFlag: false,
      obituaryUserItems: [],
      obituary: {
        id: null,
        obituaryName: null,
        obituaryAge: null,
        obituaryAgeYn: null,
        obituarySex: null,
        obituarySexYn: null,
        obituaryMortuary: null,
        obituaryCode: null,
        obituaryDieYn: null,
        obituaryDieDate: null,
        obituaryEnterDate: null,
        obituaryCasketDate: null,
        obituaryCasketYn: null,
        obituaryReligionDate: null,
        obituaryReligionYn: null,
        obituaryEndDate: null,
        obituaryPlace: null,
        obituaryText: null,
        page: {
          page: this.$route.query.page ? this.$route.query.page : 0,
          size: this.$route.query.size ? this.$route.query.size : 8,
          sort: this.$route.query.sort ? this.$route.query.sort : "regDate,desc"
        }
      },
      endFlag: this.$route.query.endFlag ? this.$route.query.endFlag : "ing",
      searchTxt: this.$route.query.searchTxt ? this.$route.query.searchTxt : "",
      searchType: this.$route.query.searchType
        ? this.$route.query.searchType
        : "name",
      obituaryId: "",
      pages: function() {
        const list = [];
        for (let i = this.startPage; i <= this.endPage; i += 1) list.push(i);
        return list;
      }
    };
  },
  computed: {
    ...mapGetters([
      "getObituaryList",
      "getObituaryPage",
      "getObituaryPageTotal",
      "getObituaryTotalElements",
      "getObituaryCount",
      "memberInfo"
    ]),
    startPage() {
      return parseInt((this.getObituaryPage - 1) / 5) * 5 + 1;
    },
    endPage() {
      return (parseInt((this.getObituaryPage - 1) / 5) + 1) * 5 <
        this.getObituaryPageTotal
        ? (parseInt((this.getObituaryPage - 1) / 5) + 1) * 5
        : this.getObituaryPageTotal;
    },
    bugoAddCladd() {
      if (this.endFlag === "all") {
        return "bugo-first";
      } else if (this.endFlag === "ing") {
        return "bugo-first";
      } else {
        return "";
      }
    }
  },
  created() {},
  mounted() {
    this.requestGetObituaryList();
  },
  beforeDestroy() {
    this.$store.commit("OBITUARY_INIT");
  },
  methods: {
    transClose() {
      this.mobileUserPopupFlag = false;
    },
    obituaryTrans(memberList, type) {
      if (type == "movie") {
        if (memberList.length > 0) {
          if (
            memberList[0].obituaryMemberPhone !== null &&
            memberList[0].obituaryMemberPhone !== undefined &&
            memberList[0].obituaryMemberPhone !== ""
          ) {
            if (
              confirm(
                "고객한테 제작 안내 메시지가 발송됩니다. 전송하시겠습니까? (제3자 개인정보 동의로 간주함)"
              )
            ) {
              const parameter = {
                id: memberList[0].id,
                phone: memberList[0].obituaryMemberPhone,
                obituaryMemberId: memberList[0].id
              };
              this.$store.dispatch(
                "REQUEST_OBITUARY_ALL_SEND_MOVIE_MOBILE",
                parameter
              );
            }
          } else {
            alert("첫번째 사용자의 전화번호를 입력해 주세요.");
          }
        }
      } else {
        this.mobileSmsType = type;
        this.obituaryUserItems = memberList;
        this.mobileUserPopupFlag = true;
      }
    },
    obituaryReply(memberList, id) {
      if (
        memberList !== null &&
        memberList !== undefined &&
        memberList.length > 0
      ) {
        this.mobileSmsType = "reply";
        this.obituaryUserItems = memberList;
        this.mobileUserPopupFlag = true;
        this.obituaryId = id;
      } else {
        var url = `https://obituary.jrbaksa.com/api/obituary/reply?id=${this.obituaryId}`;
        var smsMessage =
          "[답례인사 안내]\n\n상주님, 이번 상중에 조의를 표한 조문객분들께 감사 인사를 보내시도록 답례 인사 예시문을 보내드립니다\n\n 내용일 확인, 수정하신 후 답례글을 보내세요\n\n아래 url 또는 아래 버튼을 누르시면 확인 하실 수  있습니다.\n\n [답례인사 예시문]\n" +
          url +
          "\n\n다시한번 고인의 명복을 빕니다";

        var realSmsMessage = smsMessage.replace(/(\n|\r\n)/g, "%0a");

        realSmsMessage = smsMessage;
        var varUA = navigator.userAgent.toLowerCase(); //userAgent 값 얻기

        let phoneType = "";
        if (varUA.indexOf("android") > -1) {
          //안드로이드
          phoneType = "android";
        } else if (
          varUA.indexOf("iphone") > -1 ||
          varUA.indexOf("ipad") > -1 ||
          varUA.indexOf("ipod") > -1
        ) {
          //IOS
          phoneType = "ios";
        } else {
          //아이폰, 안드로이드 외
          phoneType = "other";
        }

        location.href =
          "sms:" +
          (phoneType == "ios" ? "&" : "?") +
          "body=" +
          encodeURIComponent(realSmsMessage);
      }
    },
    obituaryReg() {
      this.$router.push("/obituaryReg");
    },
    obituaryUpdate(id) {
      this.$router.push({
        path: "/obituaryUpdate",
        query: {
          id: id
        }
      });
    },
    requestGetObituaryList() {
      const parameter = {
        obituary: this.obituary,
        endFlag: this.endFlag,
        searchTxt: this.searchTxt,
        searchType: this.searchType
      };
      this.$store.dispatch("REQUEST_OBITUARY_LIST", parameter);
    },
    requestSearch(flag) {
      if (flag !== null && flag !== undefined) {
        this.endFlag = flag;
      }
      this.requestObituaryList();
    },
    onPageChange(val) {
      const param = {
        page: val
      };
      this.requestObituaryList(param);
    },
    requestObituaryList(param) {
      let currentPage = 0;
      if (
        param !== undefined &&
        param !== null &&
        param.page !== undefined &&
        param.page !== null
      ) {
        currentPage = param.page - 1;
      }
      this.$router.push({
        path: "/obituaryList",
        query: {
          endFlag: this.endFlag,
          searchTxt: this.searchTxt,
          searchType: this.searchType,
          page: currentPage,
          size: this.obituary.page.size,
          sort: this.obituary.page.sort
        }
      });
    },
    obituaryDelete(obituary) {
      if (obituary.endFlag === "Y") {
        if (
          confirm(
            "해당 부고는 현재 장례를 진행중에 있습니다. 발인 시키겠습니까?"
          )
        ) {
          const parameter = {
            obituary: {
              id: obituary.id,
              page: this.obituary.page
            },
            endFlag: this.endFlag,
            searchTxt: this.searchTxt,
            searchType: this.searchType
          };
          this.$store.dispatch("REQUEST_DO_ENDTIME_OBITUARY", parameter);
        }
      } else {
        if (confirm("해당 부고를 삭제하시겠습니까?")) {
          const parameter = {
            obituary: {
              id: obituary.id,
              page: this.obituary.page
            },
            endFlag: this.endFlag,
            searchTxt: this.searchTxt,
            searchType: this.searchType
          };
          this.$store.dispatch("REQUEST_DELETE_OBITUARY", parameter);
        }
      }
    },
    obituaryInfiDelete(obituary) {
      if (confirm("해당 부고를 삭제하시겠습니까?")) {
        const parameter = {
          obituary: {
            id: obituary.id,
            page: this.obituary.page
          },
          endFlag: this.endFlag,
          searchTxt: this.searchTxt,
          searchType: this.searchType
        };
        this.$store.dispatch("REQUEST_DELETE_OBITUARY", parameter);
      }
    },
    obituaryView(obituary) {
      this.$store.dispatch("REQUEST_ENCODE", obituary.id).then(data => {
        if (data.result) {
          window.open(
            `${process.env.VUE_APP_OBITUARY_BASEURL}/obituary?id=${data.str}`,
            "부고장"
          );
        }
      });
    }
  }
};
</script>
