<template>
  <div class="mobile_obituary_user active">
    <!-- 장례식장 사이드메뉴-->
    <div class="mobile_obituary_user_inner">
      <div class="address" style="margin-top:32px;">
        <div style="width:100%; height:440px; overflow:auto;overflow-x:hidden">
          <table cellspacing="0" cellpadding="0" border="0">
            <template v-if="mobileSmsType != 'movie'">
              <tr
                v-for="(item, index) in items"
                :key="index"
                style="color:#999; border-bottom:1px solid #ccc;"
              >
                <td width="100%" style="padding:8px 0 8px;">
                  <button
                    @click="transSms(item)"
                    style="float:left;width:50%;height:27px"
                  >
                    <p>{{ item.obituaryMemberType }}</p>
                    {{ item.obituaryMemberName }}
                  </button>
                  <button
                    v-if="
                      mobileSmsType == 'obituaryAll' ||
                        mobileSmsType == 'obituary'
                    "
                    @click="transSmsOnly(item)"
                    style="float: right;width: 22%;font-size:10px;height: 27px;background: gray;color: #ffffff;"
                  >
                    부의금 개인등록
                  </button>
                  <button
                    v-if="
                      mobileSmsType == 'obituaryAll' ||
                        mobileSmsType == 'obituary'
                    "
                    @click="transSms(item)"
                    style="float: right;width: 22%;font-size:10px;height: 27px;background: gray;color: #ffffff;margin-right:3%"
                  >
                    부고 단체등록
                  </button>
                </td>
              </tr>
            </template>
          </table>
        </div>
      </div>
    </div>

    <button class="btn_address_colse" @click="transClose()">닫기</button>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "AddressSearch",
  props: ["methods", "pitems", "transClose", "mobileSmsType", "obituaryId"],
  data() {
    return {
      address: "",
      allChecked: false,
      items: []
    };
  },
  mounted() {
    this.items = this.pitems;
  },
  computed: {},
  methods: {
    transSmsOnly(item) {
      if (this.mobileSmsType === "obituary") {
        const parameter = {
          id: item.id,
          phone: item.obituaryMemberPhone,
          obituaryMemberId: item.id
        };
        this.$store.dispatch("REQUEST_OBITUARY_SEND_ONLY_MOBILE", parameter);
      } else if (this.mobileSmsType === "obituaryAll") {
        const parameter = {
          id: item.id,
          phone: item.obituaryMemberPhone,
          obituaryMemberId: item.id
        };
        this.$store.dispatch(
          "REQUEST_OBITUARY_ALL_SEND_ONLY_MOBILE",
          parameter
        );
      }
    },
    transSms(item) {
      if (this.mobileSmsType === "obituary") {
        const parameter = {
          id: item.id,
          phone: item.obituaryMemberPhone,
          obituaryMemberId: item.id
        };
        this.$store.dispatch("REQUEST_OBITUARY_SEND_MOBILE", parameter);
      } else if (this.mobileSmsType === "movie") {
        const parameter = {
          id: item.id,
          phone: item.obituaryMemberPhone,
          obituaryMemberId: item.id
        };
        this.$store.dispatch(
          "REQUEST_OBITUARY_ALL_SEND_MOVIE_MOBILE",
          parameter
        );
      } else if (this.mobileSmsType === "obituaryAll") {
        const parameter = {
          id: item.id,
          phone: item.obituaryMemberPhone,
          obituaryMemberId: item.id
        };
        this.$store.dispatch("REQUEST_OBITUARY_ALL_SEND_MOBILE", parameter);
      } else if (this.mobileSmsType === "reply") {
        var url = `https://obituary.jrbaksa.com/api/obituary/reply?id=${this.obituaryId}`;
        var smsMessage =
          "[답례인사 안내]\n\n상주님, 이번 상중에 조의를 표한 조문객분들께 감사 인사를 보내시도록 답례 인사 예시문을 보내드립니다\n\n 내용일 확인, 수정하신 후 답례글을 보내세요\n\n아래 url 또는 아래 버튼을 누르시면 확인 하실 수  있습니다.\n\n [답례인사 예시문]\n" +
          url +
          "\n\n다시한번 고인의 명복을 빕니다";

        var realSmsMessage = smsMessage.replace(/(\n|\r\n)/g, "%0a");

        realSmsMessage = smsMessage;
        var varUA = navigator.userAgent.toLowerCase(); //userAgent 값 얻기

        let phoneType = "";
        if (varUA.indexOf("android") > -1) {
          //안드로이드
          phoneType = "android";
        } else if (
          varUA.indexOf("iphone") > -1 ||
          varUA.indexOf("ipad") > -1 ||
          varUA.indexOf("ipod") > -1
        ) {
          //IOS
          phoneType = "ios";
        } else {
          //아이폰, 안드로이드 외
          phoneType = "other";
        }

        location.href =
          "sms:" +
          item.obituaryMemberPhone +
          (phoneType == "ios" ? "&" : "?") +
          "body=" +
          encodeURIComponent(realSmsMessage);
      }
    }
  }
};
</script>
